$bigShadow: 0 8px 16px rgba(0, 0, 0, 0.28);
$shadow: 0 4px 8px rgba(0, 0, 0, 0.28);
$yellowBackground: rgb(26, 72, 196);
$elioBackground: #fad331;
$twixBackground: #CF9FFF;

body {
    font-family: neue-haas-grotesk-display;
}

.theSongs {}

.songGrid {
    max-width: 1050px;
    margin: 25px auto;
    display: grid;
    grid-template-columns: 1fr;
    gap: 15px;
    padding: 15px;

    @media(min-width: 720px) {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }

    >a {
        text-decoration: none;
        color: black;
        display: flex;
        flex-direction: row;
        padding: 5px;
        border: 1px solid gray;
        border-radius: 5px;
        gap: 15px;

        >img {
            width: 25%;
            height: 100%;
        }

        >div {
            width: 75%;
            align-self: center;

            >h2 {
                margin: 0;
                font-size: 1.2rem;
            }

            >h3 {
                margin: 0;
                font-size: 1rem;
                font-weight: normal;
            }
        }
    }

    >a:hover {
        background: lightgray;
    }
}

.drawerHeader {
    h2 {
        span {
            text-transform: uppercase;
            font-size: 2rem;
            font-weight: bolder;
        }

        font-size: 1.2rem;
        font-weight: normal;
    }
}

.yellow {

    .videoContainer {
        margin: 10px auto;
    }

    background-image: url(../public/yellow_background.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 50vh;
    width: 100%;
    padding: 25px 0;
    padding-bottom: 50px;

    >.logo {
        margin: 0 auto;
        display: block;
        max-width: 400px;
        width: 50%;
        text-align: center;
    }

    .yellowBoxContainer {
        max-width: 1050px;
        margin: 0 auto;
        margin-top: 25px;
        padding: 0 25px;
    }

    .yellowTopThree {
        margin: 15px 0;
        margin-top: 25px;

        .yellowTopThreeItem {
            background-color: #D9D9D9;
            border: 1px solid #D9D9D9;
            border-radius: 5px;
            box-shadow: $shadow;
            margin-bottom: 25px;
        }

        .yellowTopThreeTop {
            background-color: white;
            width: 100%;
            display: grid;
            gap: 15px;
            grid-template-columns: 2fr 5fr;

            >img {
                width: 100%;
                height: auto;
            }

            h2,
            h3,
            h4,
            h3>div {
                font-weight: normal;
                margin: 5px 0;
                text-wrap: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            h2 {
                font-weight: bold;
                font-size: 1.5rem;
                line-height: 1.6rem;
                display: flex;

                span:first-child {
                    margin-right: 5px;
                }
            }

            h3,
            h3>div {
                margin-bottom: 0;
                font-weight: bold;
            }

            h4 {
                margin-top: 0;
            }

            .new {
                line-height: normal;
                align-self: center;
            }

            .loser,
            .riser,
            .same,
            .award {
                width: 1.4rem;
                height: 1.4rem;
            }

        }

        .threeColSummary {
            display: grid;
            gap: 25px;
            grid-template-columns: 2fr 3fr 3fr;
            padding: 10px;
            text-align: center;

            h4 {
                margin: 0;
                text-transform: uppercase;
                font-size: 0.6rem;
                text-align: center;
                font-weight: normal;
                text-wrap: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            p {
                font-size: 2rem;
                margin: 10px 0;
            }

        }

        @media(min-width: 720px) {
            display: grid;
            gap: 25px;
            grid-template-columns: 1fr 1fr;
            min-height: 0;
            /* NEW */
            min-width: 0;
            width: 100%;

            .yellowTopThreeItem {
                margin-bottom: 0;
                min-width: 0;
            }
        }

        @media(min-width: 900px) {
            grid-template-columns: 1fr 1fr 1fr;
        }
    }

    .yellowBox {
        background-color: white;
        padding: 15px;
        box-shadow: $bigShadow;
        border-radius: 10px;
        padding-bottom: 60px;
        position: relative;

        a.viewChartLink {
            position: absolute;
            bottom: 0;
            left: 0;
        }

        .yellowBoxHeader {
            >div {
                text-align: center;
            }

            >div:first-child,
            >div:nth-child(3) {
                background-color: #FFD521;
            }

            h2 {
                color: #FFD521;
                line-height: 2.5rem;
                font-size: 2rem;
                text-transform: uppercase;
                text-align: center;
                margin: 0;
            }

            .social {
                text-align: left;
                display: flex;
                margin: 20px 0;

                >a {
                    text-decoration: none;
                    color: black;
                    font-weight: bold
                }

                >img {
                    display: inline-block;
                    max-height: 20px;
                    margin-right: 5px;
                }
            }


            @media(min-width: 720px) {
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;

                .social.twitter {
                    flex-direction: row;
                    justify-content: flex-end;
                }

                .social.youtube {
                    flex-direction: row;
                    justify-content: center;
                }
            }

        }

    }

}

.navigation {
    background-color: black;
    padding: 5px;
    position: sticky;
    top: 0;
    z-index: 5;

    .horizontal {
        height: fit-content;
        padding-left: 120px;
        overflow: hidden;
        z-index: 0;
        position: relative;
        max-width: 1050px;
        margin: 0 auto;
        position: relative;
        box-sizing: border-box;
        text-align: left;
    }

    .horizontal>.navigationLogo {
        display: block;
        background-image: url(../public/logo.png);
        width: 100px;
        height: 40px;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        position: absolute;
        z-index: 25;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
    }


    .navigationTabList {
        overflow: hidden;
    }

    @media(min-width: 768px) {
        .horizontal {
            padding-left: 150px;
        }

        .horizontal>.navigationLogo {
            width: 125px;
            height: 50px;
        }
    }


    >div>div>button:after,
    >div>div>button:hover:before {
        bottom: 5px;
    }
}


.header {
    padding: 25px;
    max-width: 1050px;
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;

    h1 {
        margin-top: 5px;
        text-align: center;
        padding-bottom: 2.1875rem;
        padding-top: 2.5rem;
        letter-spacing: .063rem;
        font-size: 3rem;
        line-height: 3rem;

        @media(min-width: 920px) {
            font-size: 5.0625rem;
            line-height: 1.875rem;
        }
    }

    h2 {
        color: rgb(130, 137, 161);
        margin-top: 0;
        margin-bottom: 0;
        padding-top: 0;
    }

    h3,
    h3>div {
        font-size: 1.6875rem;
        line-height: 1.875rem;
        letter-spacing: 0.03375rem;
        word-break: break-word;
    }

    .columns.noVideo {
        @media(min-width: 821px) {

            gap: 2.5%;
            display: block;
            grid-template-columns: 3fr 4fr;
            vertical-align: top;
            margin-bottom: 1rem;
        }
    }

    .columns {

        .videoContainer {
            margin-top: 15px;
        }

        >div:last-child {
            display: none;
        }

        >div:last-child:first-child {
            display: block;
        }

        @media(min-width: 821px) {

            gap: 2.5%;
            display: grid;
            grid-template-columns: 3fr 4fr;
            vertical-align: top;
            margin-bottom: 3rem;

            >div:last-child {
                border-left: 1px solid black;
                display: block;
                padding-left: 5%;
            }

            >div:last-child:first-child {
                border-left: none;
                max-width: 400px;
                margin: 0 auto;
            }

        }

        div.summary {
            padding-right: 5%;
            display: grid;
            padding-bottom: 5%;
            border-bottom: 1px solid black;
            grid-template-columns: 1fr 3fr 2fr;

            img {
                width: 100%;
                height: auto;
            }

            >div:first-child {
                font-size: 5.4375rem;
                font-weight: 900;
                margin-top: -1.25rem;
                text-align: center;
                margin-bottom: 1.25rem;
            }

        }
    }
}

.chartNavigation {
    justify-content: space-between;
    border-top: 6px solid;
    border-bottom: .188rem solid;
    padding-top: 1rem;
    padding-bottom: 1rem;
    display: grid;
    grid-template-columns: 4fr 1fr 4fr;
    width: 100%;
    margin-bottom: 56px;
    align-items: center;

    >a {
        color: rgb(26, 72, 196);
        font-size: 0.9375rem;
        line-height: 1.5625rem;
        font-weight: 900;
        text-transform: uppercase;
        text-decoration: none;

    }

    >div {
        justify-content: center;
        margin: 0 auto;
    }

    >a:last-child {
        text-align: right;
    }

}

.chartNavigation>a.right {
    text-align: right;
}


.home {

    background-color: rgb(237, 235, 235);
    min-height: calc(100vh - 64px);

    >.homeIntro {
        padding: 15px 25px;
        max-width: 1050px;
        margin: 0 auto;
        box-sizing: border-box;

        @media(min-width: 821px) {
            padding: 15px 5px;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 0 25px;
        }


    }

    section.homeChartsBackground {
        background-image: url(../public/allchartsbackground.png);
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        padding-bottom: 50px;
    }

    .homeCharts {

        max-width: 1050px;
        margin: 0 auto;
        padding: 25px;
        padding-top: 50px;

        >div {

            display: flex;
            flex-direction: column;
            box-shadow: $bigShadow;

        }

        >div>h2 {

            border-top-left-radius: 5px;
            border-top-right-radius: 5px;
            font-size: 1.6875rem;
            line-height: 1.875rem;
            letter-spacing: 0.03375rem;
            font-weight: 900;
            margin: 0;
            color: white;
            padding: 8px;
            width: 100%;
            background-color: #005581;
            text-align: center;
            box-sizing: border-box;
        }

        >div {
            border-radius: 5px;
            background-color: white;
            box-sizing: border-box;
            margin-bottom: 25px;
        }

        @media(min-width: 720px) {

            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 15px;

            >div {
                margin-bottom: 0;
            }
        }

        @media(min-width: 860px) {

            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 15px;
            align-items: end;

            >div {
                margin-bottom: 0;
            }
        }

    }
}

.chartCompactSkeleton {
    width: 100%;
    height: 100;
}


.compactTable {
    display: table;
    table-layout: fixed;
    border-collapse: collapse;
    width: 100%;

    .compactRow {

        display: table-row;
        border-collapse: collapse;

        >div {
            display: table-cell;
            vertical-align: middle;
            border-bottom: 1px solid lightgray;
        }

        >div:nth-child(1) {
            width: 10.5%;
            text-align: right;
            font-size: 2rem;
            font-weight: 900;
        }

        >div:nth-child(2) {
            width: 14.5%;
            text-align: center;

            >span {
                max-width: 20px;
            }

            .new {
                max-width: 100%;
                font-size: 0.6rem;
                padding: 0.1rem 0.2rem;
            }
        }

        >div:nth-child(3) {
            width: 50%;

            >h3,
            >p,
            >h3>div {
                margin: 0;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                max-width: 95%;
            }
        }

        >div:nth-child(4) {

            padding-top: 25%; position: relative;
            width: 25%;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;

            img {
                position: absolute; top: 0; left: 0;
                width: 100%;
                height: auto;
                display: block;
                border-bottom: none;
            }
        }


    }

    .compactRow:last-child {
        >div {
            border-bottom: none;
        }
    }

    .compactRow.loadingCompactRow {
        >div {
            width: 100%;
            padding: 25px 15px;
        }
    }
}



.table {

    .exceptExport
    {
    
        margin: 0 auto;
    
        @media(min-width: 821px) {

            text-align: right;
            max-width: 1050px;
    
            >div>div>h2 {
                font-size: 1.125rem;
            }
    
            >div>div>p {
                font-size: 0.9375rem;
            }
        }
    
    }
    

    background-color: rgb(237, 235, 235);
    width: 100%;
    font-family: neue-haas-grotesk-display;
    padding: 1rem 15px;
    box-sizing: border-box;

    .dropOuts {
        gap: 0 15px;

        >div {

            text-align: left;
            margin-bottom: 15px;
            background-color: white;
            border: 1px solid #ff69b4;
            border-radius: 5px;
            display: inline-grid;
            width: 100%;
            grid-auto-columns: max-content;
            gap: 0 15px;
            grid-auto-flow: column;

            >img {
                display: block;
            }

            position: relative;

            >div {
                margin-right: 15px;
                align-self: center;

                >h2 {
                    margin-bottom: 5px;
                    font-size: 1.025rem
                }

                >p {
                    margin-top: 0;
                    font-size: 0.8rem;
                }

                /*> h2, > p { white-space: nowrap; overflow: hidden; text-overflow: ellipsis;  }*/
            }
        }

        margin: 0 auto;

        @media(min-width: 821px) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            max-width: 1050px;

            >div>div>h2 {
                font-size: 1.125rem;
            }

            >div>div>p {
                font-size: 0.9375rem;
            }
        }
    }

    >.row:hover {
        background-color: #f5f5f5;
        cursor: pointer;
    }

    >h2 {
        display: block;
        padding-top: 1.5rem;
        padding-bottom: 1rem;
        max-width: 1050px;
        margin: 15px auto;
        margin-bottom:0;
        font-size: 1.875rem;
        line-height: 1.875rem;
        margin-top:5px;

        >span {
            margin-top:10px;
            float: none;
            display: block;
            text-transform: uppercase;
            text-align: left;
            font-weight: normal;
            font-size: 1rem;
            line-height: 1.875rem;
            //line-height: 1rem;

        }

        @media(min-width: 821px) {

            margin-bottom:15px;
            margin-top:0;

            > span {
                margin-top:0;
                display: inline;
            float: right;
            text-align: right;
            }

        }

        > span.hasSwitch{
            line-height: 1.1rem;
            label { padding-right:0;}
        }
    }

    .row {
        >div {
            display: none;

            >img {
                width: 100%;
                height: auto;
                display: block;
            }

            border-bottom: 1px solid black;
        }

        >div:nth-child(1),
        >div:nth-child(2),
        >div:nth-child(3),
        >div:nth-child(4) {
            display: table-cell;
        }

        >div:nth-child(3) {
            width: 14%;
            text-align: center;
        }

        >div.thin:nth-child(1) {
            width: 10%;

            @media(min-width: 821px) {
                font-size: 2rem;
            }
        }

        @media(min-width: 821px) {

            >div:nth-child(6),
            >div:nth-child(7),
            >div:nth-child(8) {
                font-size: 1.0625rem;
                line-height: 1.4375rem;
                letter-spacing: 0.03375rem;
                font-weight: 500;
            }
        }


        >div:nth-child(2) {
            width: 16%;
        }

        .row>div:nth-child(4) {
            width: 60%;
        }

        >div:first-child {
            font-size: 1.5rem;
            font-weight: 900;
            background-color: black;
            color: white;
            text-align: center;
            border-bottom: 1px solid white;
        }

        @media(min-width: 821px) {
            >div:first-child {
                font-size: 2rem;
            }
        }


        >div:nth-child(5),
        >div:nth-child(7) {
            background-color: rgb(237, 235, 235);
            transition: background-color 0.5s;
        }



        >div:nth-child(3) {
            padding-left: 0rem;
            padding-right: 0rem;
        }

        >div:first-child {
            font-size: 1.5rem;
            font-weight: 900;
            background-color: black;
            color: white;
            text-align: center;
            border-bottom: 1px solid white;
        }

    }

    .row:nth-child(3)>div:nth-child(2) {
        width: 25%;
    }

    .row:nth-child(3)>div:nth-child(4) {
        width: 51%;
    }

    .row:nth-child(3)>div:nth-child(6),
    .row:nth-child(3)>div:nth-child(7),
    .row:nth-child(3)>div:nth-child(8) {
        font-size: 2rem;
        font-weight: 900;
    }


    .row>div>h3,
    .row>div>h3>div {
        max-width: calc(100% - 55px);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

    }

    .row:nth-child(3)>div h3 {
        font-size: 1.2rem;
        font-weight: 600;

        @media(min-width: 821px) {
            font-size: 1.4375rem;
        }
    }

    .row:nth-child(3)>div span.artist {
        font-size: 1rem;
        max-width: calc(100% - 40px);

        @media(min-width: 821px) {
            font-size: 1.25rem;
        }
    }

    .row>div>span.artist {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        max-width: calc(100% - 55px);
        display: block;
    }


    @media(min-width: 821px) {

        .row>div:nth-child(2) {
            width: 11%;
        }

        .row>div:nth-child(3) {
            width: 7%;
        }

        .row:nth-child(3)>div:nth-child(2) {
            width: 21%;
        }

        .row>div:nth-child(4) {
            width: 42%;
        }

        .row:nth-child(3)>div:nth-child(4) {
            width: 32%;
        }
    }



    .panel {

        position: relative;

        h2 {
            text-transform: uppercase;
        }


        background-color: white;
        margin: 0 auto;
        width: 100%;
        border-bottom: 1px solid black;
        padding: 25px 10px;



        @media(min-width: 821px) {
            max-width: 1050px;
            margin: 0 auto;
            padding: 25px 25px;

        }

        box-sizing: border-box;
        border-left: 1px solid grey;
        border-right: 1px solid grey;

    }


    .row>div:nth-child(4) {
        border-right: 1px solid grey;
        position: relative;

        .expandIcon {
            min-width: 25px;
            text-align: center;
            font-weight: 600;
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);
            cursor: pointer;
            color: grey;
            font-size: 2.5em;
        }
    }

    @media(min-width: 821px) {

        .row>div:nth-child(4) {
            border-right: none;
        }

        .row>div:last-child {
            border-right: 1px solid grey;
        }
    }

    .row.noBorder>div {
        border-bottom: none;
        background-color: white;
    }

    .row.noBorder>div:first-child {
        background-color: black;
    }

    .row.noBorder {
        >div>span.expandIcon {
            color: #005581;
        }
    }

    .row.noBorder {
        >div:nth-child(5) {
            color: #005581;
        }

        >div:nth-child(6),
        >div:nth-child(7),
        >div:nth-child(8) {
            font-weight: bold;
            color: #005581;
        }
    }

    .row.highlightRow {
        transition: all 0.5s;
        background-color: #005581 !important;

        >div:nth-child(5),
        >div:nth-child(7) {
            transition: background-color 0.5s;
            background-color: #004471;
        }

        color: white;

        >div>span.expandIcon {
            color: white;
        }
    }

    .row {
        transition: all 0.5s;
    }

    .row,
    .headerRow {
        clear:both;
        background-color: white;
        margin: 0 auto;
        display: table;
        table-layout: fixed;
        width: 100%;

        @media(min-width: 821px) {
            max-width: 1050px;
            margin: 0 auto;
        }


        >div {
            vertical-align: middle;
        }

        @media(min-width: 821px) {
            >div {
                display: table-cell;
            }
        }

    }

    .row.lastRow {
        >div:first-child {
            border-bottom-left-radius: 5px;
        }

        >div:last-child {
            border-bottom-right-radius: 5px;
        }
    }

    .headerRow {

        @media(min-width: 821px) {
            position: sticky;
            top: 64px;
            z-index: 5;
        }

        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        background-color: rgb(26, 72, 196);
        color: white;

        >.thinnest {
            display: none;
        }

        >div:nth-child(1) {
            display: table-cell;
            width: 100%;
            text-align: center;
        }

        >div:first-child {
            text-align: center;
        }

        background-color: rgb(26, 72, 196);
        color: white;

        >div {
            font-size: 0.71875rem;
            font-weight: bold;
            line-height: 1.125rem;
            text-transform: uppercase;
            padding-top: 0.75rem;
            padding-bottom: .75rem;
        }

        @media(min-width: 821px) {

            //> div { font-size: 1.0625rem;}
            >div.thinnest {
                display: table-cell;
            }

            >div:nth-child(1) {
                width: 66%;
                text-align: left;
                padding-left: 1rem;
            }

            >div:nth-child(2) {
                display: none;
            }
        }
    }

}

h3 {
