.circleIcon {
    background-color: #005581;
    color: white;
    width: 44px;
    height: 44px;
    border-radius: 22px;
    position: relative;

    >svg {
        top: 50%;
        left: 50%;
        position: absolute;
        transform: translate(-50%, -50%);
    }
}

.circleIcon:hover {
    cursor: pointer;
    background-color: #004471;
}


a.viewChartLink {

    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;

    line-height: 1.875rem;
    display: block;
    letter-spacing: 0.03375rem;
    font-weight: 900;
    margin: 0;
    color: white;
    padding: 8px;
    width: 100%;
    background-color: black;
    text-align: center;
    box-sizing: border-box;
    font-size: 1.25rem;
    text-transform: uppercase;
    text-decoration: none;
    position: relative;

    span {
        padding-left: 10px;
        color: red;
        font-weight: normal;
        font-size: 1.5rem;
    }
}


a.viewChartLink:hover {
    background-color: rgb(50, 50, 50);
}

.videoContainer {
    max-width: 500px;

    .videoEmbed {

        width: 100%;
        padding-top: 56.25%;
        position: relative;

        >iframe {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            border: none;
            outline: none;
            box-shadow: $shadow;
        }
    }
}

.data {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 15px 0;
    border-bottom: 1px dashed grey;

    >div:last-child {
        text-align: right;
        font-weight: bold;
    }
}

.data:last-child {
    border-bottom: none;
}

.drawerBody{
    > h2 { margin-bottom: 4px;}
}

.chartRunHeader {

    margin-top: 15px;
    h2 { text-transform: uppercase;}

    @media(min-width: 821px) {
        display: grid;
        grid-template-columns: 3fr 1fr;
        align-items: center;
    }

    .key {
        display: block;
        margin-bottom: 25px;

        @media(min-width: 821px) {
            margin-bottom: 0;
        }
    }

    >div {
        >div {
            line-height: 20px;
            font-size: 0.8rem;
            padding-left: 25px;
            position: relative;
        }

        >div:before {
            content: " ";
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 18px;
            height: 18px;
            background-color: #005581;
        }

        >div.mostRecent:before {
            content: " ";
            background-color: #ff69b4;
        }

        >div.peakPosition:before {
            content: " ";
            background-color: white;
            border: 1px solid #005581;
            width: 16px;
            height: 16px;
        }

    }

}


.history {
    margin-top: 15px;
    margin-bottom: 25px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    grid-auto-columns: min-content;
    grid-auto-flow: column;

    >a,
    >div {
        font-family: neue-haas-grotesk-display;
        border: 1px solid grey;
        padding: 15px;
        text-align: center;
        min-width: 50px;
        display: block;
        text-decoration: none;
        color: black;
    }

    >.isPeak {
        border-color: #005581;
        border-width: 3px;
        padding: 13px;
    }

    >.currentSelectedWeek {
        background-color: #005581;
        color: white;
    }

    >.currentWeek {
        background-color: #ff69b4;
        color: white;
    }

    >.isPeak.currentSelectedWeek {
        border-color: #005581;
    }
}

.switchContainer {
    clear:left;
    display: inline-block; background-color: rgb(237, 235, 235);
    margin-bottom: 10px; margin-top: 0;
    .switch {background-color: rgb(237, 235, 235);}
}

